@mixin initial-flex {
	display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
	justify-content: flex-start;
	align-content: flex-start;
	align-items: flex-start; }

@mixin absolute-wrapper {
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0; }

/* Шрифты */
@mixin font-face($family, $path, $weight: normal, $style: normal) {
  @font-face {
    font-family: $family;
    src: url('#{$path}.woff2') format('woff2'), url('#{$path}.woff') format('woff');
    font-weight: $weight;
    font-style: $style; } }

//Regular
@include font-face('neosanspro', '../fonts/neosanspro-regular', $weight: 400);
//Medium
@include font-face('neosanspro', '../fonts/neosanspro-medium', $weight: 500);
//Light
@include font-face('neosanspro', '../fonts/neosanspro-light', $weight: 100);
//Italic
@include font-face('neosanspro', '../fonts/neosanspro-italic', $weight: 400, $style: italic);
//Light Italic
@include font-face('neosanspro', '../fonts/neosanspro-lightitalic', $weight: 100, $style: italic);
// Bold
@include font-face('neosanspro', '../fonts/neosanspro-bold', $weight: 700);
// Bold italic
@include font-face('neosanspro', '../fonts/neosanspro-bolditalic', $weight: 700, $style: italic);
// Ultra
@include font-face('neosanspro', '../fonts/neosanspro-ultra', $weight: 800);


body, html {
	$templateWidth: 1360px !global;
	$borderHint: 1px solid #f00 !global;
