.callback-link__area {
	width: 100%;
	position: absolute;
	bottom: 0;
	left: 0;
	right: 0;
	text-align: center;
	margin-left: 58px; }
.callback__link, .write-review {
	color:  $headerTextDefaultColor;
	font-weight: 400;
	vertical-align: top;
	font-size: 14px;
	line-height: 1.06;
	&:hover {
		span::after {
			opacity: 0; } }
	& span {
		height: 25px;
		display: inline-block; }
	& span::after {
		content: "";
		display: block;
		width: 100%;
		height: 2px;
		background-image: -moz-linear-gradient(left, $underlineColor, $underlineColor 60%, transparent 60%);
		background-image: -ms-linear-gradient(left, $underlineColor, $underlineColor 60%, transparent 60%);
		background-image: -o-linear-gradient(left, $underlineColor, $underlineColor 60%, transparent 60%);
		background-image: -webkit-gradient(linear, 0 0, 100% 0, from($underlineColor), color-stop(0.6, $underlineColor), color-stop(0.6, transparent));
		background-image: -webkit-linear-gradient(left, $underlineColor, $underlineColor 60%, transparent 60%);
		background-image: linear-gradient(left, $underlineColor, $underlineColor 60%, transparent 60%);
		background-size: 10px 1px;
		background-repeat: repeat-x;
		background-position: 0 90%;
		margin-top: 10px;
		opacity: 1;
		transition-property: opacity;
		transition-timing-function: ease-in;
		transition-duration: 300ms; }
	&::before {
		content: '';
