$transformXTomato: 0;
$rotateAngleMeat: 0;
$transformXMeat: 0;
$transformYMeat: 0;
@for $i from 1 through 30 {
	$rotateAngleTomato: $rotateAngleTomato + 5deg;
	$scaleGreetingTomato: $scaleGreetingTomato - 0.01;
